.Gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  grid-gap: 10px;
}

.Gallery__Square {
  position: relative;
}

.Gallery__Square:before {
  display: block;
  content: '';
  width: 100%;
  padding-top: 100%;
}

.Gallery__Square > * {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.Gallery__Item {
  background: #fff;
  border: 1px solid #000;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1), 0 1px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1), 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.Gallery__Item:hover .Gallery__Overlay {
  opacity: 1;
}

.Gallery__Background {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 10px;
}

.Gallery__Overlay {
  background: #152129;
  color: #fff;
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 20px;
  border-radius: 4px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: 0;
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease;
}

.Gallery__Title {
  font-size: 20px;
}

.Gallery__Desc {
  padding-top: 5px;
  font-size: 14px;
}



